/* ================== HELPERS  =================== */
a:hover {
    text-decoration: none;
}

.img-wrap {
    text-align: center;
    display: block;
}

.img-wrap img {
    max-width: 100%;
}

.icon-wrap {
    text-align: center;
}

.zoom-in {
    transition: transform ease .3s;
}

.zoom-wrap:hover .zoom-in {
    transform: scale(1.1);
    transition: transform ease .3s;
}

/* =================  DEFINED VARIABLES =================== */
.icon-xs, .icon-sm, .icon-md, .icon-lg {
    display: inline-block;
    text-align: center;
}

.icon-xs {
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 16px;
}

.icon-sm {
    width: 42px;
    height: 42px;
    line-height: 42px;
    font-size: 22px;
}

.icon-md {
    width: 60px;
    height: 60px;
    line-height: 60px;
    font-size: 32px;
}

.icon-lg {
    width: 80px;
    height: 80px;
    line-height: 80px;
    font-size: 42px;
}

.img-xs, .img-sm, .img-md, .img-lg {
    -o-object-fit: cover;
    object-fit: cover;
}

