/* by default 24 grid cols full width */
[class^="col-"][class*="-24"] {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

/* nogutter for 24 grid cols */
.no-gutters > [class*="col-"] {
    padding-right: 0 !important;
    padding-left: 0 !important;
}

/* ================== SECTIONS =================== */
.navbar-top {
    padding: 0;
}

.navbar-landing {
    transition: .5s;
    padding-top: 15px;
    padding-bottom: 15px;
    position: fixed;
    top: 0;
    z-index: 100;
    width: 100%;
}

.navbar-landing.fixed-top {
    transition: .5s;
    padding-top: 7px;
    padding-bottom: 7px;
}

/* ================== header main ==================  */
.header-main {
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.brand-wrap .logo, .navbar-brand .logo {
    margin-right: 7px;
    max-height: 42px;
    display: inline-block;
}

.brand-wrap .logo-text, .navbar-brand .logo-text {
    margin: 0;
    display: inline-block;
    font-size: 22px;
    vertical-align: middle;
}

.header-main {
    position: relative;
}

.widget-header {
    margin-left: 7px;
    margin-right: 7px;
    display: inline-block;
    position: relative;
}

.widget-header form {
    min-width: 280px;
}

.widget-header .title {
    display: block;
}

.widget-header .notify {
    position: absolute;
    top: -3px;
    right: -10px;
}

/* desktop only */
/* desktop only .end */
/* ================ SECTION-FOOTER ==================  */
