/* ================= lists ================= */
[class*="list-"] li:after {
    visibility: hidden;
    display: block;
    content: "";
    overflow: hidden;
    height: 0;
    clear: both;
}

.list-lg li {
    margin-bottom: 10px;
}

.list-icon {
    list-style: none;
    padding: 0;
}

.list-icon li {
    margin-bottom: 10px;
