.list-icon .icon {
    width: 22px;
    margin-right: 15px;
    vertical-align: middle;
    font-size: 20px;
    text-align: center;
}

.list-icon span {
    vertical-align: middle;
}

.list-bullet {
    list-style: none;
    padding: 0;
}

.list-bullet li {
    margin-bottom: 7px;
    position: relative;
    padding-left: 15px;
}

.list-bullet li::before {
    content: " ";
    position: absolute;
    top: 45%;
    left: 0;
    width: 7px;
    height: 7px;
    margin: 0px 10px 0px 0px;
    background: #6c757d;
    border-radius: 7px;
}

